<template>
  <div class="container" style="background-color: #DAEBE3">
    <div>
      <movie-index-header></movie-index-header>
    </div>
    <div class="contentPic">
      <el-carousel :interval="4000" type="card" height="300px">
        <el-carousel-item v-for="item in headerItems" v-bind:key="item._id" name="aaa" style="width: 800px">
          <!--      <h3 class="medium">{{ item }}</h3>-->
          <div class="headerPic">
            <div style="height: 200px;padding-left: 50px;padding-right: 50px ;border-radius: 30px;">
              <!--          <p class="imgTitle">{{recommendTitle}}</p>-->
              <img v-bind:src=item.recommendImg class="headerImg" v-on:click="checkVideoFun(item.recommendSrc)"/>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
      <!--      <index-header-pic v-for="item in headerItems" v-bind:key = "item._id" v-bind:recommendImg="item.recommendImg" v-bind:recommendSrc="item.recommendSrc" v-bind:recommendTitle="item.recommendTitle"></index-header-pic>-->
    </div>
    <!--              //外层的遮罩 v-if用来控制显示隐藏 点击事件用来关闭弹窗-->
    <div class='mask' v-if='videoState == true' @click='masksCloseFun'></div>
    <!--              //弹窗-->
    <div class="videomasks" v-if="videoState == true">
      <!--                //视频：h5的视频播放video-->
      <video :src='videoSrc' controls='controls' autoplay :options="playerOptions">
      </video>
    </div>
    <div class="contentMain">
      <div class="contentLeft" style="display: inline-block;text-align: left;margin-left: 130px">
        <!--          <ul class="cont-ul">-->
        <movies-list v-for="item in movieItems" v-bind:key="item._id" v-bind:id="item._id"
                     v-bind:movieName="item.movieName" :movieNumSuppose="item.movieNumSuppose"
                     v-bind:movieImg="item.movieImg"></movies-list>
        <!--          </ul>-->
      </div>
    </div>
  </div>
</template>

<script>
import MovieIndexHeader from '../components/MovieIndexHeader'
import MoviesList from '../components/MoviesList'
import IndexHeaderPic from '../components/IndexHeaderPic'

export default {
  name: 'index',
  data() {
    return {
      headerItems: [],
      movieItems: [],
      // dialogVisible: false,
      videoState: false,
      videoSrc:'',
      playerOptions:{
        width: document.documentElement.clientWidth,
        notSupportedMessage: '此视频暂无法播放，请稍后再试'
      }
    }
  },
  components: {
    // CommonFooter,
    IndexHeaderPic,
    // UserMessage,
    MovieIndexHeader,
    MoviesList
  },
  // 获取数据
  created() {
    //获取主页推荐电影
    this.$http.get('http://kr.xj173.cn:5050/showIndex').then((data) => {
      this.headerItems = data.body.data;
      console.log(data.body.data)
    })
    //获取电影
    this.$http.get('http://kr.xj173.cn:5050/getIndexMovie').then((data) => {
      this.movieItems = data.body.data;
      console.log(data.body.data)
    })
  },
  methods: {
    checkVideoFun(videos) {
      this.videoState = true;
      this.videoSrc = videos;
    },
    masksCloseFun() {
      this.videoState = false;
    }
  }

}
</script>

<style lang="css" scoped>
.container {
  width: 100%;
  margin: 0 auto;
}

.container {
  background-color: #efefef;
  min-height: 100%;
}

.contentMain {
  /*padding-top: 15px;*/
  text-align: center;
  /*padding: initial;*/
  /*float: left;*/
  display: inline-block;
  margin: auto;
}

.userMessage {
  margin-top: 0px;
  margin-left: 0px;
}

.contentPic {
  padding-top: 5px;
}

.contentLeft {
  text-align: center;
  margin: auto;
}

.headerPic {
  height: 200px;
  /*width: 100%;*/
  /*background-color: white;*/
}

.headerImg {

  height: 300px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.cont-ul {
  padding-top: 0.5rem;
  background-color: #fff;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  background-color: #000000;
  opacity: .6;
}

/*// 内容层 z-index要比遮罩大，否则会被遮盖*/
.videomasks {
  max-width: 1200px;
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 20;
  transform: translate(-50%, -50%);
}

.videomasks video {
  width: 100%;
  height: 100%;
}

.cont-ul::after {
  content: '';
  display: block;
  clear: both;
  width: 0;
  height: 0;
}
</style>



